import React from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { styled } from '@mui/material/styles';

//icons
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';


import {changeTexture} from '../../../aframe-components/AdminComponents/changeMat';
import {changeLogo} from '../../../aframe-components/AdminComponents/changeMat';
import { updateLocales } from '../../../../server/UpdateLocal';


export default function MainLocal(props) {
    const [infoTabOpen, setInfoTabOpen] = React.useState(true);
    const [imageData, setImageData] = React.useState();

    const [namePabellon, setNamePabellon] = React.useState();
    const [descripPabellon, setDescripPabellon] = React.useState();
    const [videoPabellon, setVideoPabellon] = React.useState();

    React.useEffect(() => {
        //let imagen = getLogo()
       //setImageData(imagen)

       setNamePabellon(props.arr.nombre_pabellon)
       setDescripPabellon(props.arr.descripcion_pabellon)
       setVideoPabellon(props.arr.video_pabellon)

    }, []);

    const getLogo = (local) =>{
        let logoimage = null
        let logopanel = document.getElementById('logo-Local'+props.number)
        logopanel.object3D.traverse(function (node) {
            if (node.isMesh) {
              
              //node.material = mat
              if(node.material.map){
                logoimage = node.material.map?.image?.currentSrc
              }
              
            }
          })
        
    
        return logoimage
    }

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
      }));

    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "15%",

        padding: '2rem',
    }

    const myStyle = {
        display: "flex",
        height: "100%",
        minWidth: "25%",
        padding: '1rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        minWidth: "50%",

        padding: '1rem'
    }
    const myStyle3 = {
        display: "flex",
        height: "100%",
        width: "33%",
        flexDirection: "column",
        padding: '1rem 1rem'
    }

    const myColorStyle = {
        display: "flex",
        height: "100%",
        width: "33%",
        padding: '0.5rem 1rem'
    }

    const contButton = {
        display: "flex",
        height: "100%",
        width: "10%",
        flexDirection: "column",
        justifyContent:"flex-end",
        padding: '0.5rem 1rem'
    }

    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }

    const colorContainer = {
        width: "100%",
        display:"flex",
        flexWrap:"wrap"
        
    }

    const textures = [
        "brick.jpg",
        "wood.jpg",
        "marble.jpg",
    ]

    const plt = [
        "#15133C",
        "#73777B",
        "#EC994B",
        "#F1EEE9",
        "#112B3C",
        "#205375",
        "#251D3A",
        "#2A2550",
        "#F66B0E",
        "#FF7700",
        "#EFEFEF",
        "#ea660e",
        "#5ac7d1",
    ]


    function changeTab(e) {
        //console.log(e)
        var tabInf = document.getElementById("tab-inf");
        var tabApa = document.getElementById("tab-apa");
        //console.log(e.target.id)

        if (e.target.id === "btn-inf") {
            tabInf.classList.add("o-tab-active")
            tabApa.classList.remove("o-tab-active")
            setInfoTabOpen(true)
        } if (e.target.id === "btn-apa") {
            tabInf.classList.remove("o-tab-active")
            tabApa.classList.add("o-tab-active")
            setInfoTabOpen(false)
        }

    }

    const handleSave = () => {
        //console.log(props.arrobject)
        let mylocal = "Pabellón"+props.number
        updateLocales(mylocal, props.arr, namePabellon, descripPabellon, videoPabellon)
        
    };


    return (
        <Box sx={{ width: '90%', height: '80%', backgroundColor: '#676767', display: 'flex' }}>

            {/* Pestañas superiores */}
            <Box sx={{ width: "auto", height: "1.8rem", position: "absolute", top: "0", display: "flex" }}>
                <Box id="tab-inf" className="o-tab-active" sx={pestanaStyle}><Button sx={{ padding: 0 }} variant="text" onClick={changeTab} id="btn-inf">Información</Button></Box>
                <Box id="tab-apa" className="" sx={pestanaStyle}><Button variant="text" sx={{ padding: 0 }} onClick={changeTab} id="btn-apa">Archivos</Button></Box>
            </Box>

            <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
                <Box sx={backDivStyle}>
                    <button style={styleButton} onClick={props.handleClickBack}>
                        <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
                        <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
                    </button>


                </Box>
                <Divider orientation="vertical" variant="middle" flexItem />
                {infoTabOpen &&
                    <>
                        <Box sx={myStyle}>
                            <Box sx={{ width: "40%", height: "70%" }}>
                                <img src={require("../../../../assets/images/Locales/Local2_1.jpg")} style={{ width: "100%", height: "100%", objectFit: "scale-down" }}></img>
                            </Box>
                            <Box>
                                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Pabellón</Typography>
                                <Typography sx={{ color: 'white' }} variant='h3'>{"#"+props.number}</Typography>
                            </Box>

                        </Box>
                        <Divider orientation="vertical" variant="middle" flexItem />
                        <Box sx={myStyle2}>
                            <Box sx={{ width: "30%" }}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Nombre</Typography>
                                <input id="nombre-input" className="o-input" type="text" defaultValue={namePabellon} onChange={(e)=>{setNamePabellon(e.target.value)}}></input>
                            </Box>
                            <Box sx={{ width: "45%", marginLeft: "0.5rem" }}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Descripción</Typography>
                                <input id="descrip-input" className="o-input" type="text" style={{ height: "3rem", width: "100%" }} defaultValue={descripPabellon} onChange={(e)=>{setDescripPabellon(e.target.value)}}></input>
                            </Box>

                        </Box>
                    </>
                }

                {!infoTabOpen &&
                    <>
                        <Box sx={myStyle3}>
                            <Typography sx={{ color: 'white' }} variant='h6'>URL video pabellón</Typography>
                            <input id="video-input" className="o-input" type="text" defaultValue={videoPabellon} onChange={(e)=>{setVideoPabellon(e.target.value)}}></input>

                        </Box>
                        <Divider orientation="vertical" variant="middle" flexItem />
                        
                        <Box sx={contButton}>
                            <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#cc293c", height:"3rem" }}>
                                Guardar
                            </ColorButton> 
                        </Box>
                    </>
                }

            </Box>

        </Box>
    )
}