import axios from "axios";

import { getPubli } from "./DataPubli";
import {getPabellones} from "./DataLocal"
import { getFeria } from "./DataFeria";

import { addProducts } from "./Pruebas";
/* Ingresa
SE obtienen los locales - se guarda arreglo
Se ajusta los colores locales

 */

//  Ingreso con un usuario genérico de hoyostools para poder hacer las consultas
export const loginEventVR = () => {

    window.localStorage.removeItem('adminToken');
    window.localStorage.removeItem('idEU');
    window.localStorage.removeItem('localsArray');
    window.localStorage.removeItem('arrpubli');
    window.localStorage.removeItem('arrayStands');
   

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
    
    const bodyLogin = {
        "request_data": {
        "type_action": "login",
            "data": {
                "correo_usuario": "adminferia@eventvr.com",
                "clave_usuario": "123456"
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json"
        }
    }

    // Axios
    axios.post(url + 'login', bodyLogin, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
    .then(async(response) => {
        console.log('respuesta :', response.data.status)
       
        if(response.status == '200' && response.data.status != 'failed'){
            
            let EventVRToken = response.data.result.token;
            let idEmpresa = response.data.result.user_data.id_empresa_usuario;
            window.localStorage.setItem('adminToken', EventVRToken);
            //console.log('idempresa: ', response.data.result)
            
            getFeria(EventVRToken,"94e9a01a-cb87-4be6-a4f8-9893a08034d4",idEmpresa)
            getPabellones(EventVRToken, "94e9a01a-cb87-4be6-a4f8-9893a08034d4") //idferia
            getPubli(EventVRToken)

            

        }
    })
    .catch(error => {
        console.log('error : ', error)
    });
}
