import { ConstructionOutlined } from "@mui/icons-material";
import axios from "axios";

export const updateLocales = (local,obj, nombre, descripcion, video) =>{

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

   
    
    const body ={
        "request_data": {
          "type_action": "update_pa",
          "data": {
            "id_pabellon":obj.id_pabellon,
            "nombre_pabellon": nombre,
            "descripcion_pabellon": descripcion,
            "video_pabellon":video,
            "disponibilidad_pabellon":"true",
            "id_feria_pabellon": obj.id_feria_pabellon
          }
        },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization": window.localStorage.getItem('adminToken')
      }
    }

    // Axios
    axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'marketplaces', body, {
      headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
      },
    })
    .then((response) => {
        

      if(response.status == '200' && response.data.status != 'failed' && !response.data.errorMessage){
        
        console.log(response)
        alert("Se ha guardado correctamente en la base de datos")

      }else{
        alert(response.data.errorMessage)
      }
    })
    .catch(error => {
        console.error('error : ', error)
        alert(error)
    });

  }
