import React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';

import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';

import { useTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

import addLogo from '../../../assets/images/logos_loacles/addLogo.jpg'
import videoLogo from '../../../assets/images/logos_loacles/video.png'


//  COMPONENTES UI
// import ShoppingCart from './ShoppingCart';

export default function VideoModal() {
    const [open, setOpen] = React.useState(false);
    const [linkVideo, setLinkVideo] = React.useState("");
    const [panelEl, setPanelEl] = React.useState();
    
    const inputEl = React.useRef(null);
    const [videoData, setVideoData] = React.useState();
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    React.useEffect(() => {
        console.log("use effect modal")
        document.addEventListener('open_video_modal', function (e) {
            //console.log(e.detail.element);
            setPanelEl(e.detail.element)
            setLinkVideo(e.detail.element.getAttribute('src'))
            //console.log(e.detail.element.getAttribute('src'))



            handleClickOpen();
        })


    }, []);








    const BootstrapDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
            padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
            padding: theme.spacing(1),
        },
    }));

    const BootstrapDialogTitle = (props) => {
        const { children, onClose, ...other } = props;

        return (
            <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
                {children}
                {onClose ? (
                    <IconButton
                        aria-label="close"
                        onClick={onClose}
                        sx={{
                            position: 'absolute',
                            right: 8,
                            top: 8,
                            color: (theme) => theme.palette.grey[500],
                        }}
                    >
                        <CloseIcon />
                    </IconButton>
                ) : null}
            </DialogTitle>
        );
    };

    BootstrapDialogTitle.propTypes = {
        children: PropTypes.node,
        onClose: PropTypes.func.isRequired,
    };

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
    }));

    const contentStyle = {
        minHeight: "10rem",
        width: "100%",
        display: "flex",
        justifyContent: "space-around"
    }


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleSave = () => {
        setOpen(false);
        

        let mylink = document.getElementById('link-input').value
        //console.log(mylink)
        panelEl.setAttribute('src', mylink)

        if(mylink!==''){
            panelEl.children[0].setAttribute('src',videoLogo)
        }else{
            panelEl.children[0].setAttribute('src',addLogo)
        }
        

    }

    return (
        <div>
            {/* <Button variant="outlined" className='interactable-ui' onClick={handleClickOpen}>
        Open responsive dialog
      </Button> */}
            {/* <ShoppingCart/> */}
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    Cambiar Video
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>
                    {/* <Box sx={{ width: "60%", height: "160px", backgroundColor: "gray" }}>
                        <img src={imageData} style={{ width: "100%", height: "100%", objectFit: "contain" }}></img>
                    </Box> */}
                    <Box>

                        <TextField
                            variant="filled"
                            id="link-input"
                            label="Link video"
                            defaultValue={linkVideo}
                        />

                    </Box>
                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#cc293c" }}>
                        Aplicar
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>
        </div>
    );
}