import axios from "axios";

export const getFeria = (token, idFeria, idEmpresa) =>{

  

  const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
  
  const body ={
    "request_data": {
      "type_action": "get_one_fe",
      "data": {
        "id_feria": idFeria,
        "id_empresa_usuario_feria": idEmpresa
      }
    },
    "headers": {
      "Content-Type": "application/json",
      "Accept": "application/json",
      "Authorization": token
    }
  }

  // Axios
  axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'marketplaces', body, {
    headers: {
        "Content-Type": "application/json",
        "Accept": "application/json",
    },
  })
  .then((response) => {
      //console.log(response)

    if(response.status == '200' && response.data.status !== 'failed' /* && response.data.status */){

        const dataLobby = response.data.body.data;
      // Obtengo la entidad aframe del local
      const lobbyEntity = document.querySelector('#lobby-model');
      if(lobbyEntity){
        //Agrego el componente encargado de setear las características de los locales
        lobbyEntity.setAttribute('lobby-data', {
          lobbyData: JSON.stringify(dataLobby[0])
        })

        //window.localStorage.setItem('localsArray', JSON.stringify(dataLobby));

       

      }
    }
  })
  .catch(error => {
      console.error('error : ', error)
  });

}