import axios from "axios";

export const updateBrochure = (obj, nombre, urlBrochure) => {

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    const body = {
        "request_data": {
            "type_action": "update",
            "data": {
                "id_brochure": obj.id_brochure,
                "id_stand_brochure": obj.id_stand_brochure,
                "nombre_brochure": nombre,
                "url_brochure": urlBrochure,
                "ubicacion_brochure": obj.ubicacion_brochure
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization": window.localStorage.getItem('adminToken')
        }
    }

    // Axios
    axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'brochures', body, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
        .then((response) => {
            console.log(response)

            if (response.status == '200' && response.data.status !== 'failed' && !response.data.errorMessage) {

                //** */
                alert("Actualizado correctamente en la base de datos")


            } else {
                //console.log("error")
            }
        })
        .catch(error => {
            console.error('error : ', error)
        });

}

