import React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';

import DialogTitle from '@mui/material/DialogTitle';

import { useTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import Tooltip from '@mui/material/Tooltip';
import Slide from '@mui/material/Slide';
import CircularProgress from '@mui/material/CircularProgress';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import EditRoundedIcon from '@mui/icons-material/EditRounded';
import DeleteForeverRoundedIcon from '@mui/icons-material/DeleteForeverRounded';

import { getBrochures } from '../../../server/DataBrochures';
import { updateBrochure } from '../../../server/UpdateBrochures';
import { deleteBrochure } from '../../../server/UpdateBrochures';
import { addBrochure } from '../../../server/UpdateBrochures';
import { display } from '@mui/system';

//  COMPONENTES UI
// import ShoppingCart from './ShoppingCart';

export default function BorchuresModal() {
    const [open, setOpen] = React.useState(false);
    const [loadBtn, setLoadBtn] = React.useState(false);
    const [isLoading, setIsLoading] = React.useState(true);
    const [panelEl, setPanelEl] = React.useState();
    const [list, setList] = React.useState(null);
    const [idStand, setIdStand] = React.useState(null);

    const [elSelect, setElSelect] = React.useState();

    const [openEdit, setOpenEdit] = React.useState(false);
    const [openAdd, setOpenAdd] = React.useState(false);
    const [openDelete, setOpenDelete] = React.useState(false);

    const theme = useTheme();


    React.useEffect(() => {

        document.addEventListener('open_brochures_modal', function (e) {
            //console.log(e.detail.element.getAttribute('idStand'));

            setList(null)
            setIdStand(null)
            setIsLoading(true)


            let thisid = e.detail.element.getAttribute('idStand')
            setIdStand(thisid)
            setPanelEl(e.detail.element)
            setLoadBtn(false)


            handleClickOpen();


            getB(thisid)

        })



    }, []);

    function getB(id) {

        let broch = getBrochures(id)
        broch.then((response) => {
            //console.log(response)

            if (response.status == '200' && response.data.status !== 'failed' && !response.data.errorMessage && response.data.body?.status === 'success') {

                const dataBrochures = response.data.body.data;
                setList(dataBrochures)
                setIsLoading(false)


            } else {
                //console.log("error")
                setIsLoading(false)
            }
        })
            .catch(error => {
                console.error('error : ', error)
            });
    }


    const Transition = React.forwardRef(function Transition(props, ref) {
        return <Slide direction="up" ref={ref} {...props} />;
    });


    const BootstrapDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
            padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
            padding: theme.spacing(1),
        },
    }));

    const BootstrapDialogTitle = (props) => {
        const { children, onClose, ...other } = props;

        return (
            <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
                {children}
                {onClose ? (
                    <IconButton
                        aria-label="close"
                        onClick={onClose}
                        sx={{
                            position: 'absolute',
                            right: 8,
                            top: 8,
                            color: (theme) => theme.palette.grey[500],
                        }}
                    >
                        <CloseIcon />
                    </IconButton>
                ) : null}
            </DialogTitle>
        );
    };

    BootstrapDialogTitle.propTypes = {
        children: PropTypes.node,
        onClose: PropTypes.func.isRequired,
    };

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
    }));

    const inputs = {
        width: "100%",
        marginBottom: "1rem"
    }

    const contentStyle = {
        minHeight: "10rem",
        width: "100%",
        padding: "0.5rem"

    }

    const Container = {
        width: "100%",
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        border: "2px solid gray",
        borderRadius: "8px",
        padding: "5px",
        marginBottom: "0.5rem"
    }

    const btnCont = {
        /* height:"1rem", */
        width: "5rem",
        /* backgroundColor:"red", */
        display: "flex",
        justifyContent: "center"
    }


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setLoadBtn(false)
    };

    const handleClickOpenEdit = () => {
        setOpenEdit(true);
        setOpen(false);
    };

    const handleCloseEdit = () => {
        setOpenEdit(false);
        setOpen(true);

    };

    const handleSaveEdit = () => {
        setOpenEdit(false);
        //UPDATE BROCHURE
        let nombreB = document.getElementById('name-input-edit').value
        nombreB = nombreB === '' ? "Sin nombre" : nombreB

        let urlB = document.getElementById('url-input-edit').value

        updateBrochure(elSelect, nombreB, urlB)

    };

    const handleOpenAdd = () => {
        setOpenAdd(true);
        setOpen(false)
    };

    const handleCloseAdd = () => {
        setOpenAdd(false);
        setOpen(true)

    };

    const handleSaveAdd = () => {
        setOpenAdd(false);
        //ADD brochure

        let nombreB = document.getElementById('name-input-add').value
        nombreB = nombreB === '' ? "Sin nombre" : nombreB

        let urlB = document.getElementById('url-input-add').value

        addBrochure(idStand, nombreB, urlB)

    };

    const handleOpenDelete = () => {
        setOpenDelete(true);
        setOpen(false);
    };

    const handleCloseDelete = () => {
        setOpenDelete(false);
        setOpen(true);

    };

    const handleSaveDelete = () => {
        setOpenDelete(false);
        //DELETE BROCHURE

        deleteBrochure(elSelect)

    };



    function loadBrochures() {
        let arr = list
        let rows = [];


        if (arr) {
            //arr = JSON.parse(arr)

            arr?.forEach(element => {
                rows.push(
                    <Box sx={Container} key={element.id_brochure}>
                        <Typography>{element.nombre_brochure}</Typography>
                        <Box sx={btnCont}>
                            <Tooltip title="Editar">
                                <IconButton onClick={() => {
                                    setElSelect(element)
                                    handleClickOpenEdit()
                                }}><EditRoundedIcon color="info" /></IconButton>
                            </Tooltip>
                            <Tooltip title="Eliminar">
                                <IconButton onClick={() => {
                                    setElSelect(element)
                                    handleOpenDelete()
                                }}><DeleteForeverRoundedIcon color="error" /></IconButton>
                            </Tooltip>

                        </Box>
                    </Box>
                )
            });


        }




        return <>{rows}</>
    }

    return (
        <div>

            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    Brochures
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>
                    {isLoading &&
                        <Box sx={{ display: 'flex', width:"100%", height:"100%", justifyContent:"center", alignItems:"center" }}>
                            <CircularProgress color="info"/>
                        </Box>
                    }
                    {!isLoading &&
                        loadBrochures()
                    }
                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleOpenAdd} sx={{ backgroundColor: "#cc293c" }}>
                        Agregar brochure
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>

            {/* Edit ************************************************************************/}
            <BootstrapDialog
                onClose={handleCloseEdit}
                aria-labelledby="customized-dialog-title"
                open={openEdit}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleCloseEdit}>
                    Editar brochure
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>

                    <TextField
                        variant="filled"
                        id="name-input-edit"
                        label="Nombre"
                        defaultValue={elSelect?.nombre_brochure}
                        sx={inputs}
                    />
                    <TextField
                        variant="filled"
                        id="url-input-edit"
                        label="url"
                        defaultValue={elSelect?.url_brochure}
                        sx={inputs}
                    />

                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleSaveEdit} sx={{ backgroundColor: "#cc293c" }}>
                        Guardar
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>

            {/* Add **************************************************************************** */}

            <BootstrapDialog
                onClose={handleCloseAdd}
                aria-labelledby="customized-dialog-title"
                open={openAdd}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleCloseAdd}>
                    Agregar brochure
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>

                    <TextField
                        variant="filled"
                        id="name-input-add"
                        label="Nombre"
                        defaultValue=""
                        sx={inputs}
                    />
                    <TextField
                        variant="filled"
                        id="url-input-add"
                        label="url"
                        defaultValue=""
                        sx={inputs}
                    />

                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleSaveAdd} sx={{ backgroundColor: "#cc293c" }}>
                        Agregar
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>


            {/* ELIMINAR ********************************************************************** */}

            <BootstrapDialog
                open={openDelete}
                TransitionComponent={Transition}
                fullWidth={true}
                maxWidth='sm'
                onClose={handleCloseDelete}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogTitle>{"¿Está seguro de que desea eliminar este elemento?"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                        Se eliminarán todos los datos asociados a este elemento, esta acción no se puede revertir.
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button color="secondary" variant="contained" onClick={handleCloseDelete}>Volver</Button>
                    <Button color="error" variant="contained" onClick={handleSaveDelete}>Borrar</Button>
                </DialogActions>
            </BootstrapDialog>


        </div>
    );
}