import React from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';

//icons
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import { changeColorStand } from '../../../aframe-components/AdminComponents/changeMat';

import { updateShelf } from '../../../../server/UpdateShelf';



export default function MainEstanteria(props) {


    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "13%",

        padding: '2rem 0.5rem',
    }

    const myStyle = {
        display: "flex",
        height: "100%",
        width: "33%",
        padding: '0rem 0.5rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        width: "50%",
        padding: '0.5rem 1rem'
    }

    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }

    const colorContainer = {
        width: "100%",
        display: "flex",
        flexWrap: "wrap"

    }

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
      }));

    const plt = [
        "#0a0a0a",
        "#15133C",
        "#73777B",
        "#EC994B",
        "#112B3C",
        "#205375",
        "#251D3A",
        "#2A2550",
        "#FF7700",
        "#FFFFFF",
        "#ea660e",
        "#243D25",
        "#062C30",
        "#83BD75",
        "#B22727",
        "#630606",
        "#FFE61B",
        "#F27777"
    ]

    function fillColors(meshMat) {
        var rows = [];

        var clave = props.arrobject.ubicacion_stand
        //let clave = 1

        for (var i = 0; i < plt.length; i++) {
            let pos = i
            //No funciona el envío del arreglo en posición i ***revisar
            rows.push(<button key={i} onClick={function () { changeColorStand(plt[pos], meshMat, clave) }}
                className="o-button-style"
                style={{ height: "1rem", width: "1rem", backgroundColor: plt[i] }}>
            </button>);
        }
        return <div style={colorContainer}>{rows}</div>;
    }

    const handleSave = () => {
        //console.log(props.arrobject)
        let nombre = document.getElementById('input-nombre-stand').value
        updateShelf(props.arrobject, nombre)
        
        
    };


    return (


        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
            <Box sx={backDivStyle}>
                <button style={styleButton} onClick={()=>props.handleClickBack(props.arrobject.ubicacion_stand)}>
                    <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
                    <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
                </button>


            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Box sx={myStyle}>
                <Box sx={{ width: "45%", height: "70%" }}>
                    <img src={require("../../../../assets/images/prev_estantes/" +props.imgshelf)} style={{ width: "100%", height: "100%", objectFit: "scale-down" }}></img>
                </Box>
                <Box sx={{ width: "55%", height: "70%", marginTop: "2rem" }}>
                    <Typography sx={{ color: 'white', fontSize: "0.9rem" }} variant='h6'>Stand tipo 1</Typography>
                    <Typography sx={{ color: 'white', fontSize: "0.9rem" }} variant='h6'>Nombre:</Typography>
                    <input id="input-nombre-stand" style={{ width: "80%" }} className="o-input" type="text" defaultValue={props.arrobject.nombre_stand}></input>
                </Box>

            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Box sx={myStyle2}>
                <Box sx={{ marginRight: "1rem", }}>
                    <Typography sx={{ color: 'white' }} variant='h6'>Color 1</Typography>
                    {fillColors("Mostrador2")}
                </Box>
                <Box>
                    <Typography sx={{ color: 'white' }} variant='h6'>Color 2</Typography>
                    {fillColors("SoporteTV1")}
                </Box>
                <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#cc293c", height:"3rem" }}>
                    Guardar
                </ColorButton>
            </Box>

        </Box>


    )
}