import React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import CategoryIcon from '@mui/icons-material/Category';
import { useTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import { getProduct } from '../../../server/Data';
import addLogo from '../../../assets/images/logos_loacles/addLogo.jpg'

import { updateProduct } from '../../../server/UpdateProduct';

//  COMPONENTES UI
// import ShoppingCart from './ShoppingCart';

export default function ProductModal() {
    const [open, setOpen] = React.useState(false);
    const [loadBtn, setLoadBtn] = React.useState(false);
    const [codigo, setCodigo] = React.useState("");
    const [panelEl, setPanelEl] = React.useState();
    const [nameProduct, setNameProduct] = React.useState();
    const inputEl = React.useRef(null);
    const [imageData, setImageData] = React.useState();
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const [nombreProducto, setNombreProducto] = React.useState("");
    const [descripcionProducto, setDescripcionProducto] = React.useState("");

    React.useEffect(() => {

        document.addEventListener('open_product_modal', function (e) {
            //console.log(e.detail.element.children);
            setPanelEl(e.detail.element)
            setLoadBtn(false)

            if (e.detail.element.children[0].getAttribute('src').charAt(0) == '#') {
                setImageData(document.querySelector(e.detail.element.children[0].getAttribute('src')).getAttribute('src'))
            } else {
                setImageData(e.detail.element.children[0].getAttribute('src'))
            }
            //setImageData(e.detail.element.children[0].getAttribute('src'))
            setNameProduct(e.detail.element.getAttribute('nameProduct'))
            handleClickOpen();


            let jsonP = JSON.parse(e.detail.element.getAttribute('arrobj'))
            setNombreProducto(jsonP.nombre_producto)
            setDescripcionProducto(jsonP.descripcion_producto)
        })

        document.addEventListener('load_product', function (e) {
            //console.log(e.detail.element.children);
            //setPanelEl(e.detail.element)
            toDataURL(
                e.detail.imagen,
                function (dataURL) {
                    setImageData(dataURL)
                },
            )
            //setImageData(e.detail.imagen)
            setNameProduct(e.detail.nombre)
            setLoadBtn(true)

        })


    }, []);

    function toDataURL(src, callback) {
        var image = new Image()
        image.crossOrigin = 'Anonymous'

        image.onload = function () {
            var canvas = document.createElement('canvas')
            var context = canvas.getContext('2d')
            canvas.height = this.naturalHeight
            canvas.width = this.naturalWidth
            context.drawImage(this, 0, 0)
            var dataURL = canvas.toDataURL('image/jpeg')
            callback(dataURL)
        }
        image.src = src
    }


    function changeInput(e){
    
        const reader = new FileReader();
        
        reader.addEventListener("load", () => {
            const uploaded_image = reader.result;
            
            
            setImageData(uploaded_image)
            
            //changeLogo(uploaded_image)
            //console.log(uploaded_image)
            
        });
        reader.readAsDataURL(e.target.files[0]);
  }




    const BootstrapDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
            padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
            padding: theme.spacing(1),
        },
    }));

    const BootstrapDialogTitle = (props) => {
        const { children, onClose, ...other } = props;

        return (
            <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
                {children}
                {onClose ? (
                    <IconButton
                        aria-label="close"
                        onClick={onClose}
                        sx={{
                            position: 'absolute',
                            right: 8,
                            top: 8,
                            color: (theme) => theme.palette.grey[500],
                        }}
                    >
                        <CloseIcon />
                    </IconButton>
                ) : null}
            </DialogTitle>
        );
    };

    BootstrapDialogTitle.propTypes = {
        children: PropTypes.node,
        onClose: PropTypes.func.isRequired,
    };

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
    }));

    const contentStyle = {
        minHeight: "10rem",
        width: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-around"
    }

    const fieldsContainer = {
        width: "100%",
        display: "flex",
        justifyContent: "space-around"
    }


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setLoadBtn(false)
    };

    const handleSave = () => {
        setOpen(false);
        setLoadBtn(false)

        if (imageData == addLogo) {
            //console.log(imageData)
            alert("Inserte una imagen válida")

        } else{
            //llAMAR UPDATEPRODUCT

            let nombre = document.getElementById("name-input").value
            let descripcion = document.getElementById("desc-input").value

            console.log(nombre)
            console.log(descripcion)
            updateProduct(JSON.parse(panelEl.getAttribute('arrobj')), imageData, codigo, false, nombre, descripcion)
            panelEl.children[0].setAttribute("src", imageData)

        }

        //console.log(imageData)


        //panelEl.children[0].setAttribute("src",require('../../../assets/images/logos_loacles/hoyostools.png'))
        panelEl.setAttribute("nameProduct", nameProduct)
    }

    const handleLoadP = () => {
        // console.log(document.getElementById('outlined-required'))
        setCodigo(document.getElementById('outlined-required').value)
        getProduct(document.getElementById('outlined-required').value)
    }

    return (
        <div>
            {/* <Button variant="outlined" className='interactable-ui' onClick={handleClickOpen}>
        Open responsive dialog
      </Button> */}
            {/* <ShoppingCart/> */}
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    Ingresar producto
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>
                    <Box sx={{ width: "100%", display:"flex"}}>
                        <Box sx={{ width: "60%", height: "160px" }}>
                            <img src={imageData} style={{ width: "100%", height: "70%", objectFit: "contain" }}></img>
                            {/* <Typography sx={{ textAlign: 'center', color: 'gray' }} variant='h6'>{nameProduct}</Typography> */}
                        </Box>
                        <Box>
                            <button className="o-button-style custom-input-file" style={{ display: "flex", height: "auto", marginLeft: "0.2rem" }}>
                                <FolderRoundedIcon sx={{ mr: 1, color: "gray", fontSize: "1.2rem" }} />
                                <Typography sx={{ color: 'gray', fontSize: "1.2rem" }}>Elegir archivo</Typography>
                                <input ref={inputEl} onChange={changeInput} type="file" id="image-modal-input" className="input-file" accept="image/jpeg, image/png, image/jpg">
                                </input>
                            </button>


                            <button className="o-button-style" style={{ display: "flex", height: "auto", marginLeft: "0.2rem" }}
                                onClick={() => {
                                    setImageData(require('../../../assets/images/logos_loacles/addLogo.jpg'))
                                }}>
                                <DeleteRoundedIcon sx={{ mr: 1, color: "gray", fontSize: "1.2rem" }} />
                                <Typography sx={{ color: 'gray', fontSize: "1.2rem" }}>Eliminar logo</Typography>
                            </button>


                        </Box>
                    </Box>

                    <Box sx={fieldsContainer}>
                        <TextField
                            variant="filled"
                            id="name-input"
                            label="Nombre"
                            defaultValue={nombreProducto}
                        />
                        <TextField
                            variant="filled"
                            id="desc-input"
                            label="Descripción"
                            defaultValue={descripcionProducto}
                        />
                        {/* <LoadingButton loading variant="outlined">Submit</LoadingButton> */}


                    </Box>
                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#cc293c" }}>
                        Guardar cambio
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>
        </div>
    );
}