import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

export default function PersonalCard(props) {
    const url = "https://d3iqhfs7sfm8r5.cloudfront.net/"
    
    return(
        <Box sx={{height:"3.5rem", width:"3rem", backgroundColor:"white", positon:"relative", padding:"0.2rem", marginRight:"0.5rem"}}>
            <Box sx={{position:"absolute"}}>
                <Box sx={{borderRadius:"50%", width:"1rem", height:"1rem", backgroundColor:"#ea660e", position:"absolute", top:"-0.5rem",left:"-0.5rem"}}>
                    <p style={{lineHeight:0.8,textAlign:"center", color:"white"}}>{props.number}</p>
                </Box>
            </Box>
            
            <Box sx={{width:"100%", height:"100%"}}>
                <button onClick={()=>props.handleClick(props.number, props.arrobject)} style={{width:"100%", height:"100%", padding:0, border:"none",backgroundColor:"transparent", cursor:"pointer"}}>
                    <img /* src={props.arrobject.imagen_publicidad==="imagen_publicidad"?require("../../../assets/images/logos_loacles/addLogo2.jpg"):url+props.arrobject.imagen_publicidad} */ 
                        src={require("../../../assets/images/logos_loacles/addLogo2.jpg")}
                        style={{ width: "100%", height: "100%", objectFit: "contain" }}></img>
                </button>
            </Box>
        </Box>
    )
}