import axios from "axios";

export const updateFeria = (obj, nombre, descripcion) => {

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    let color = getColor()
    let textura = getText()

    let object = JSON.parse(document.getElementById('lobby-model').getAttribute('lobby-data').lobbyData)

    const body = {
        "request_data": {
            "type_action": "update_fe",
            "data": {
                "id_feria": object.id_feria,
                "logo_feria": null, //object.logo_feria,
                "nombre_feria": object.nombre_feria,
                "descripcion_feria": object.descripcion_feria,
                "id_empresa_usuario_feria": object.id_empresa_usuario_feria,
                "color_paredes_feria": color,
                "textura_suelo_feria": textura
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization": window.localStorage.getItem('adminToken')
        }
    }

    console.log(body)
    

    // Axios
    axios.post(url + 'marketplaces', body, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
        .then((response) => {


            if (response.status == '200' && response.data.status != 'failed' && !response.data.errorMessage) {

                console.log(response)
                alert("Se ha guardado correctamente en la base de datos")

            } else {
                alert(response.data.errorMessage)
            }
        })
        .catch(error => {
            console.error('error : ', error)
            alert(error)
        });

}

const getColor = () => {

    let lobbymodel = document.getElementById('lobby-model')
    let mesh = lobbymodel.object3D
    let mycolor = "#FFFFFF"

    mesh.traverse(function (node) {
        if (node.isMesh) {
            //console.log(node.material.name)
            if (node.material.name === "Techo_Pa") {
                //console.log(node)
                mycolor = '#' + (node.material.color).getHexString()

            }


        }
    })
    return mycolor
}

const getText = () => {

    let lobbymodel = document.getElementById('lobby-model')
    let mesh = lobbymodel.object3D
    let mytext = "carpet.jpg"

    mesh.traverse(function (node) {
        if (node.isMesh) {
            //console.log(node.material.name)
            if (node.material.name === "Alfombra_Lobby") {
                //console.log(node)
                //mycolor = '#'+ (node.material.color).getHexString()
                mytext = node.material.map ? node.material.map.name : ""

            }


        }
    })
    return mytext
}