import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import CustomCard from '../../Cards/CustomCard';
import MainPabellon from './MainPabellon';

import { getShelfs } from '../../../../server/DataShelf';

//import { LocalsContext } from '../../../../dashboard';

import { posPabellones } from '../../../../server/PosLocales';
import { getStands } from '../../../../server/DataShelf';


export default function ConfigPabellones(props) {
    const [infoOpen, setInfoOpen] = React.useState(false);
    const [localSelected, setLocal] = React.useState();
    const [arrLocal, setArrLocal] = React.useState();

    //const arrayLocal = React.useContext(LocalsContext);
    

    function openLocal(clave,obj) {
        setInfoOpen(true)

        //document.getElementById("player-camera").setAttribute('camera', 'active', false)
        //document.getElementById("locales-camera").setAttribute('camera', 'active', true)

        getStands(obj)
        setLocal(clave)
        setArrLocal(obj)
        //activeLocalModel(obj.tipo_distribucion_local)
        props.setOpen(true)

        //let el = posPabellones[clave]
        //document.getElementById("locales-camera").setAttribute('orbit-controls', {minAzimuthAngle:el.minA,maxAzimuthAngle:el.maxA,target:el.target})
        let lobbyModel = document.getElementById("lobby-model")
        let pabellonModel = document.getElementById("local-model")
        let publicidad = document.getElementById("publicidad")
        let stands = document.getElementById("shelfs-3D-container")

        lobbyModel.setAttribute("visible",false)
        pabellonModel.setAttribute("visible",true)
        publicidad.setAttribute("visible",false)
        stands.setAttribute("visible",true)
        
    }

    function activeLocalModel(i){

    }

    function backLocal() {
        setInfoOpen(false)
        props.setOpen(false)

        document.getElementById("player-camera").setAttribute("camera",{active:true})
        

        let lobbyModel = document.getElementById("lobby-model")
        let pabellonModel = document.getElementById("local-model")
        let publicidad = document.getElementById("publicidad")
        let stands = document.getElementById("shelfs-3D-container")

        lobbyModel.setAttribute("visible",true)
        pabellonModel.setAttribute("visible",false)
        publicidad.setAttribute("visible",true)
        stands.setAttribute("visible",false)

    }

    function loadLocalCard(){
        let arr = null 

        try {
            arr = JSON.parse(localStorage.getItem('localsArray'))
            
          } catch (err) {
            // 👇️ SyntaxError: Unexpected end of JSON input
            console.log('error', err);
          }
        
        
        let rows = [];
        let key
        let id
        let i = 0

        //arr.sort((a, b) => a.clave_local - b.clave_local);

        arr?.forEach(el => {
            i++
            key = i
            id = el.pabellon

            rows.push(
                <CustomCard arrobject={el} size="md" key={'k'+key} number={key} type={2} image="Local2_1.jpg" handleClick={openLocal}></CustomCard>
            )

            
        });

        /* rows.push(
            <CustomCard arrobject={''} size="md" key={'k'} number={1} type={2} image="Local2_1.jpg" handleClick={openLocal}></CustomCard>
        ) */

        

        return <Box sx={{ display: "flex", alignItems: "flex-end", padding: "0.3rem" }}>{rows}</Box>


    }


    return (
        <Box sx={{ width: "97%", height: "100%", display: "flex" }}>
            <Box sx={{ width: '15%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Configuración pabellones</Typography>
            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />

            {/* Dividir en componentes */}
            <Box sx={{ width: '85%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                {!infoOpen &&
                    <Box sx={{ display: "flex", alignItems: "center", padding: "0.2rem 1rem", width: "95%", justifyContent: "flex-start", overflowX:"auto" }}>

                        {/* <CustomCard size="md" number="1" type={2} image="Local2_1.jpg" handleClick={openLocal}></CustomCard> */}
                        {loadLocalCard()}
                        
                    </Box>

                    
                }
                {infoOpen &&
                    <MainPabellon handleClickBack={backLocal} number={localSelected} arr={arrLocal}></MainPabellon>
                }


            </Box>
        </Box>
    )
}