import axios from "axios";

export const getPabellones = (token, idFeria) =>{

  window.localStorage.setItem('localsArray', '');

  const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
  
  const body ={
    "request_data": {
      "type_action": "get_all_pa",
      "data": {
        "id_feria_pabellon": idFeria
      }
    },
    "headers": {
      "Content-Type": "application/json",
      "Accept": "application/json",
      "Authorization": token
    }
  }

  // Axios
  axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'marketplaces', body, {
    headers: {
        "Content-Type": "application/json",
        "Accept": "application/json",
    },
  })
  .then((response) => {
      //console.log(response)

    if(response.status == '200' && response.data.status !== 'failed' /* && response.data.status */){
      const dataLocales = response.data.body.data;
      console.log(dataLocales)

        window.localStorage.setItem('localsArray', JSON.stringify(dataLocales));

      
    }
  })
  .catch(error => {
      console.error('error : ', error)
  });

}

export const getLocales = (token, idEmpresaUsuario) =>{

    window.localStorage.setItem('localsArray', '');

    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"
    
    const body ={
      "request_data": {
        "type_action": "get_all",
        "data": {
          "id_empresa_usuario_local": idEmpresaUsuario
        }
      },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization": token
      }
    }

    // Axios
    axios.post(/* process.env.URL_AMAZON_ENDPOINTS */ url + 'locales', body, {
      headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
      },
    })
    .then((response) => {
        console.log(response)

      if(response.status == '200' && response.data.status !== 'failed' /* && response.data.status */){
        const dataLocales = response.data.body.data;
        console.log(dataLocales)

        // Obtengo la entidad aframe del local
        const mallEntity = document.querySelector('#mall-model');
        if(mallEntity){
          //Agrego el componente encargado de setear las características de los locales
          mallEntity.setAttribute('locales-data', {
            localsData: JSON.stringify(dataLocales)
          })

          window.localStorage.setItem('localsArray', JSON.stringify(dataLocales));

          /* EVENTO ************************* */
          /* const dataLocalEvent = new CustomEvent('load_locales', { 
            detail:{
                localsDataArray: dataLocales
            } 
            });
          document.dispatchEvent(dataLocalEvent); */
          /* EVENTO ************************* */

        }
      }
    })
    .catch(error => {
        console.error('error : ', error)
    });

  }