import React, { useEffect } from 'react';

import addLogo from '../../../assets/images/logos_loacles/addLogo.jpg'
import addLogo2 from '../../../assets/images/logos_loacles/addLogo2.jpg'
import videoLogo from '../../../assets/images/logos_loacles/video.png'

import PlaneImageProduct from './PlaneProduct';

import { getProducts } from '../../../server/DataProducts';



export default function Stand1({ keyN, position, rotation, color, video, logos, id }) {

    const [visibleLogos, setVisibleLogos] = React.useState(false);
    const [visibleProducts, setVisibleProducts] = React.useState(false);

    const [productsLoaded, setProductsLoaded] = React.useState(false);
    const [firstL, setFirstL] = React.useState(false);
    const [productsList, setProductsList] = React.useState([]);
    const [brochuresList, setBrochuresList] = React.useState();


    const url = "https://d3iqhfs7sfm8r5.cloudfront.net/"

    React.useEffect(() => {

        document.addEventListener('load_shelf', function (e) {

            if (e.detail.idShelf === keyN) {
                //console.log(logos.superior1);
                if (e.detail.dataVisible === 'Products') {
                    setVisibleProducts(true)
                    setVisibleLogos(false)
                    setFirstL(true)


                } else {
                    setVisibleProducts(false)
                    setVisibleLogos(true)
                    
                    
                }
            }
        })

        document.addEventListener('leave_shelf', function (e) {

            if (e.detail.idShelf === keyN) {
                setVisibleProducts(false)
                setVisibleLogos(false)
                //console.log(logos.superior1);
            }
        })

        /**Reload shelf
         * if id == keyn
         * setproductsloaded false
         * call new function to getproducts
         * setproductsloaded true
         * 
         */


    }, []);

    React.useEffect(() => {

        if (firstL) {
            getP()
        }

    }, [firstL])

    

    function getP() {
        let aux = getProducts(id, "tipo1")
        aux.then(response => {
            if (response.status == '200' && response.data.status != 'failed') {
                //obtengo el arreglo de productos

                let productList = response.data?.body?.data || []
                console.log(productList)
                setProductsLoaded(true)
                setProductsList(productList)

            }
        }
        ).catch(err => {
            console.log(err)
        }

        )
    }

    function loadProducts() {
        let rows = [];
        let ubi
        let image

        productsList.forEach(element => {
            ubi = JSON.parse(element.posicion_producto.replace(/['"]+/g, '"'))
            image = element.imagen_producto


            rows.push(
                <PlaneImageProduct
                    position={ubi.position.x + " " + ubi.position.y + " " + ubi.position.z}
                    rotation={ubi.rotation.x + " " + ubi.rotation.y + " " + ubi.rotation.z}
                    width={ubi.width}
                    height={ubi.height}
                    image={image == 'imagen_producto' ? addLogo2 : (image == null ? addLogo2 : url + image)}
                    name={image == 'imagen_producto' ? "Producto no asignado" : "Producto asignado"} //cambiar por nombre
                    vis={visibleProducts}
                    id={element.id_producto}
                    arrobj={JSON.stringify(element)}
                    key={"k" + element.id_producto} />)
        });

        return <a-entity id="products-container">{rows}</a-entity>;
    }


    return (
        <a-entity id={"stand-" + keyN + "-container"} scale="1 1 1" position={position} rotation={rotation}>


            {/* MODELO 3D */}
            <a-entity
                id={"stand" + keyN}
                className="stand-1-model"
                scale="0.27 0.27 0.27"
                gltf-model="#model-asset-stand-1"

                initial-materials={"color1: "+color.color1+"; color2: "+color.color2}>
            </a-entity>

            {/* lOGOS */}

            <a-image
                
                id={"superior1-" + keyN}
                src={logos.superior1 == 'logo_superior_stand' ? addLogo : (logos.superior1 == null ? addLogo : url + logos.superior1)} 
                width="1.2" height="1.2"
                adjust-image="maxW:1.2; maxH:1.2"
                position="-3.952 6.798 4.326" rotation="0 0 0"
                //class={visibleLogos ? "clickable product-entity" : ""}
                class={"clickable product-entity"}
                interactable-logo
                //visible={visibleLogos}
                crossorigin="anonymous"
                side="front">
            </a-image>

            <a-image
                id={"superior2-" + keyN}
                src={logos.superior2 == 'imagen_superior_stand' ? addLogo : (logos.superior2 == null ? addLogo : url + logos.superior2)} 

                width="3.7" height="1.05"
                adjust-image="maxW:3.7; maxH:1.05"
                position="-0.257 5.716 4.233" rotation="0 0 0"
                //class={visibleLogos ? "clickable product-entity" : ""}
                class={"clickable product-entity"}
                interactable-logo
                //visible={visibleLogos}
                crossorigin="anonymous"
                side="front" >
            </a-image>
            <a-image
                id={"superior3-" + keyN}
                src={logos.superior3 == 'imagen_recepcion_stand' ? addLogo : (logos.superior3 == null ? addLogo : url + logos.superior3)} 

                width="2.0" height="0.8"
                adjust-image="maxW:2.0; maxH:0.8"
                position="-3.327 1.130 3.830" rotation="0 0 0"
                //class={visibleLogos ? "clickable product-entity" : ""}
                class={"clickable product-entity"}
                interactable-logo
                //visible={visibleLogos}
                crossorigin="anonymous"
                side="front" >
            </a-image>

            {/* VIDEO */}

            <a-image id={"videoStand-"+keyN} src={video} interactable-video class={"clickable product-entity"} material="side: front" geometry="height: 1.05; width: 1.95" position="3.953 3.08 3.944">
                <a-image id={'videopanel'+keyN} src={video!==''?videoLogo:addLogo} geometry="height: 1.05; width: 1.95" position="0 0 0.01"></a-image>
                <a-entity position="0.133 -0.918 0" scale="1.5 1.5 1.5"  class="clickable" visible="">
                    <a-image src="https://res.cloudinary.com/dxbh0pppv/image/upload/c_scale,h_512,q_10/v1471016296/play_wvmogo.png" class="clickable" height="0.25" width="0.25" position="-0.66049 0 0" material="" geometry=""></a-image>
                    <a-plane color="#000" height="0.25" width="1" position="0.05928 0 0" material="" geometry=""></a-plane>
                </a-entity>
            </a-image>



            {/* PRODUCTOS*/}

            {productsLoaded &&
                loadProducts()
            }

            {/* Brochures */}

            <a-image
                
                id="brochures-container"
                src={addLogo2}
                width="0.5" height="0.5"
                
                position="-1.079 2.368 3.432" rotation="-20 0 0"
                //class={visibleLogos ? "clickable product-entity" : ""}
                class={"clickable product-entity"}
                interactable-brochures
                //visible={visibleLogos}
                crossorigin="anonymous"
                side="front"
                visible={visibleLogos}
                idStand={id}>
            </a-image>



        </a-entity>
    );
}