export const changeFloorLobby = (texture) => {
  //console.log(color)
  let modelLobby = document.getElementById('lobby-model')
  //console.log(meshMat)
  let mesh = modelLobby.object3D

  const localTexture = new THREE.TextureLoader().load( 
    require('../../../assets/images/Lobby/Texturas/'+texture));
localTexture.wrapS = THREE.RepeatWrapping;;
localTexture.wrapT = THREE.RepeatWrapping;;
localTexture.repeat.set( 1, 1 )
localTexture.encoding = THREE.sRGBEncoding;
localTexture.name = texture
//localTexture.rotation = 0.875;
//localTexture.name = texture

  let floorMat = new THREE.MeshStandardMaterial({
      
      //color: '#FFFFFF',
      name: 'Alfombra_Lobby',
      //roughness: 0.8,
      //metalness:0.2,
      map:localTexture

  });


  mesh.traverse(function(node){
      if (node.isMesh){  
          //console.log(node.material.name)
          if(node.material.name==="Alfombra_Lobby"){
              //console.log(node)
              //console.log(node.material)
              node.material=floorMat
              //node.material.map = floorMat
              
          }

      }
  })
  //console.log(mesh)
}

export const changeWallLobby = (color) => {
  //console.log(color)
  let modelLobby = document.getElementById('lobby-model')
  //console.log(meshMat)
  let mesh = modelLobby.object3D


  let wallMat = new THREE.MeshStandardMaterial({
      
      color: color,
      name: 'Techo_Pa',
      roughness: 0.8,
      metalness:0.2,

  });


  mesh.traverse(function(node){
      if (node.isMesh){  
          //console.log(node.material.name)
          if(node.material.name==="Techo_Pa"){
              //console.log(node)

              node.material=wallMat
              
          }

      }
  })
  //console.log(mesh)
}

