import React, { useContext, useEffect, useState } from "react";

//  Importación de componentes - estantes
import Shelf3D1 from "./Shelf3D1";
import Shelf3D2 from "./Shelf3D2";
import Shelf3D3 from "./Shelf3D3";

import Stand1 from "./Stand1";

export default function LocalDistribution1() {
  const [load, setLoad] = React.useState(false);

  const [shelfsType1, setShelfsType1] = React.useState(false);
  const [type1Array, setType1Array] = React.useState();



  React.useEffect(() => {

    document.addEventListener('load_stands', function (e) {
        
        setType1Array(e.detail.shelvesDataArray)
        setShelfsType1(true)
        setLoad(false)
        
    })

  }, []);

  React.useEffect(() => {
    //Las funciones de load shelver type asincronas se hacen orden
    //Se debe esperar hasta el cambio type3Array ya que es el último
    //Se cambia el load a true para volver a mostrar
    setLoad(true)
  }, [type1Array]);

  function loadType1(){
  
    let rows = [];
    let ubi
    let key
    let id
    let colorP
    let colorB
    let logos
    let video
  
  
    for (let i = 0; i < type1Array.length; i++) {
      //console.log(type1Array)
      //let auxUbi = type1Array[i].ubicacion_gondola_tipo_1.replace(/'/g, '"')
      //console.log(auxUbi)

      
      
      ubi = JSON.parse(type1Array[i].posicion_stand.replace(/['"]+/g, '"')) 
      key = type1Array[i].ubicacion_stand
      id= type1Array[i].id_stand
      colorP = JSON.parse(type1Array[i].color_stand)
      video= type1Array[i].video_stand

  
      logos = {
        superior1: type1Array[i].logo_superior_stand,
        superior2: type1Array[i].imagen_superior_stand,
        superior3: type1Array[i].imagen_recepcion_stand,

      }
  
      rows.push(
        <Stand1 id={id} keyN={key} 
          position={ubi.position.x+" "+ubi.position.y+" "+ubi.position.z} 
          rotation={ubi.rotation.x+" "+ubi.rotation.y+" "+ubi.rotation.z}
          color = {colorP}
          video = {video}
          logos = {logos}
          key={i}/>
      )
  
      
    }
    return <>{rows}</>;
  }


    return (
      <>
        {load &&
        <>
        {shelfsType1 &&
            loadType1()
          }

      
        </>
          
        }

        {/* <Stand1 position="7.976 0.365 -13.793" rotation="0 0 0" keyN={1}></Stand1>
            <Stand1 position="7.976 0.365 12.813" rotation="0 180 0" keyN={2}></Stand1>
            <Stand1 position="23.779 0.365 -13.793" rotation="0 0 0" keyN={3}></Stand1>
            <Stand1 position="23.779 0.365 12.813" rotation="0 180 0" keyN={4}></Stand1>
            <Stand1 position="39.479 0.365 -13.793" rotation="0 0 0" keyN={5}></Stand1>
            <Stand1 position="39.479 0.365 12.813" rotation="0 180 0" keyN={6}></Stand1>
            <Stand1 position="55.330 0.365 -13.793" rotation="0 0 0" keyN={7}></Stand1> 
            <Stand1 position="55.270 0.365 12.813" rotation="0 180 0" keyN={8}></Stand1>*/}


      </>
    );
}