import axios from "axios";
import addLogo from '../assets/images/logos_loacles/addLogo.jpg'

export const updateShelf = (obj, nombre) => {
    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    let color1 = getColor(obj.ubicacion_stand, "Mostrador2")
    let color2 = getColor(obj.ubicacion_stand, "SoporteTV1")

    let color_stand = {
        color1:color1,
        color2:color2
    }
    //console.log(colorCuerpo)

    let logo_superior1 = getLogos(obj.ubicacion_stand, "superior1-")
    let logo_superior2 = getLogos(obj.ubicacion_stand, "superior2-")
    let logo_superior3 = getLogos(obj.ubicacion_stand, "superior3-")

    let video = getVideo(obj.ubicacion_stand)


    /* console.log(logo_superior1)
    console.log(logo_superior2)
    console.log(logo_superior3) */


    /* console.log(color1)
    console.log(color2) */


    const bodyUpdate = {
        "request_data": {
            "type_action": "update_st",
            "data": {
                "id_stand": obj.id_stand,
                "nombre_stand": nombre,
                "descripcion_stand": obj.descripcion_stand,
                "color_stand": JSON.stringify(color_stand),
                "textura_stand": obj.textura_stand,
                "tipo_stand": obj.tipo_stand,
                "posicion_stand": obj.posicion_stand,
                "logo_superior_stand": logo_superior1,
                "imagen_superior_stand": logo_superior2,
                "imagen_recepcion_stand": logo_superior3,
                "video_stand": video,
                "video_conferencia_stand": "",
                "id_pabellon_stand": obj.id_pabellon_stand,
                "ubicacion_stand": obj.ubicacion_stand
            }
        },
        "headers": {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization": window.localStorage.getItem('adminToken')
        }
    }

    // Axios
    axios.post(url + 'marketplaces', bodyUpdate, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
        .then(response => {
            console.log('respuesta :', response)

            if (response.status == '200' && response.data.status != 'failed') {
                alert("Actualizado correctamente en la base de datos.")

            }
        })
        .catch(error => {
            console.log('error : ', error)
        });
}


const getColor = (clave, meshMat) => {

    let modelStand = document.getElementById('stand' + clave)
    let mesh = modelStand.object3D
    let mycolor

    mesh.traverse(function (node) {
        if (node.isMesh) {
            //console.log(node.material.name)
            if (node.material.name === meshMat) {
                //console.log(node)
                mycolor = '#' + (node.material.color).getHexString()

            }
            

        }
    })
    return mycolor
}

const getLogos = (clave, pos) => {
    let imagen

    if (document.getElementById("" + pos + clave).getAttribute('src').charAt(0) == '#') {
        imagen = document.querySelector(document.getElementById("" + pos + clave).getAttribute('src')).getAttribute('src')
    } else {
        imagen = document.getElementById("" + pos + clave).getAttribute('src')
    }


    if (addLogo === imagen) {
        imagen = null
    } else if (!imagen.startsWith('data')) {
        imagen = null
    }

    return imagen

}

const getVideo = (clave) => {

    
    let panelVideo = document.getElementById('videoStand-'+clave)

    let srcVideo = panelVideo.getAttribute('src')

    if(!srcVideo){
        srcVideo=''
    }

    
    return srcVideo
}
