import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';

//icons
import { changeFloorLobby } from '../../../aframe-components/AdminComponents/changeMat';
import { changeWallLobby } from '../../../aframe-components/AdminComponents/changeMat';

import { updateFeria } from '../../../../server/UpdateFeria';


export default function AparienciaTab() {

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#cc293c",
        '&:hover': {
            backgroundColor: "#801925",
        },
      }));

    const myStyle = {
        display: "flex",
        height: "100%",
        width: "50%",
        padding: '1rem',

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        width: "30%",
        padding: '1rem',
    }

    const colorContainer = {
        width: "100%",
        display: "flex",
        flexWrap: "wrap"

    }

    const textures = [
        "floor1.jpg",
        "carpet.jpg",
        "carpet2.jpg",
        "marble.jpg",
        "col1.jpg",
        "col2.jpg",
        "col3.jpg",
        "floor2.jpg",
        "floor3.jpg"
    ]

    const plt = [
        "#0a0a0a",
        "#15133C",
        "#73777B",
        "#EC994B",
        "#112B3C",
        "#205375",
        "#251D3A",
        "#2A2550",
        "#FF7700",
        "#FFFFFF",
        "#ea660e",
        "#243D25",
        "#062C30",
        "#83BD75",
        "#B22727",
        "#630606",
        "#FFE61B",
    ]

    function fillTextures() {
        var rows = [];


        for (var i = 0; i < textures.length; i++) {
            let pos = i
            //No funciona el envío del arreglo en posición i ***revisar
            rows.push(<button key={i} onClick={()=>changeFloorLobby(textures[pos])}
                className="o-button-style"
                style={{height:"1rem",width:"1rem"}}>
                <img src={require("../../../../assets/images/Lobby/Texturas/"+textures[i])} style={{ width: "100%", height: "100%", objectFit: "cover" }}></img>
            </button>);
        }
        return <div style={colorContainer}>{rows}</div>;
    }

    function fillColors(meshMat) {
        var rows = [];


        for (var i = 0; i < plt.length; i++) {
            let pos = i
            //No funciona el envío del arreglo en posición i ***revisar
            rows.push(<button key={i} onClick={()=>changeWallLobby(plt[pos])}
                className="o-button-style"
                style={{ height: "1rem", width: "1rem", backgroundColor: plt[i] }}>
            </button>);
        }
        return <div style={colorContainer}>{rows}</div>;
    }

    const handleSave = () => {
        //console.log(props.arrobject)
        
        updateFeria()
        
    };

    return (
        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
            <Box sx={myStyle}>

                <Box sx={{ marginRight: "1rem", }}>
                    <Typography sx={{ color: 'white' }} variant='h6'>Suelo</Typography>
                    {fillTextures("Suelo")}
                </Box>
                <Box>
                    <Typography sx={{ color: 'white' }} variant='h6'>Paredes</Typography>
                    {fillColors("Pared")}
                </Box>

            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Box sx={myStyle2}>
                <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#cc293c", height:"3rem" }}>
                    Guardar
                </ColorButton> 
            </Box>

        </Box>
    );
}