import React from 'react';



import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import CustomCard from '../../Cards/CustomCard';
import MainEstanteria from './MainEstanteria';

import { config_shelf } from '../../../scripts/EstanteriasAdmin';
import { leave_shelf } from '../../../scripts/EstanteriasAdmin';

export default function ConfigEstanterias() {

    const [infoOpen, setInfoOpen] = React.useState(false);
    const [infoShelf, setInfoShelf] = React.useState();
    const [imgShelf, setImgShelf] = React.useState();

    function openEstanteria(id, obj) {
/** */
        let ubi = JSON.parse(obj.posicion_stand)

        setImgShelf("Prev_Estante_1.jpg")
/** */


        


        config_shelf(id, "Logos")
        setInfoOpen(true)
        setInfoShelf(obj)
        document.getElementById("estantes-camera").setAttribute('camera', 'active', true)
        document.getElementById("estantes-camera").setAttribute('orbit-controls', 'target',{x:ubi.position.x-30,y:ubi.position.y-(-3),z:ubi.position.z})
        //console.log(x)
    }

    function backEstanteria(id) {
        
        setInfoOpen(false)
        leave_shelf(id)
        document.getElementById("player-camera").setAttribute("camera", { active: true })
    }


    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }

    function loadCards() {
        const arr = JSON.parse(localStorage.getItem('arrayStands'));
        
        
        let rows = [];
        let key
        let id

        arr?.sort((a, b) => a.ubicacion_stand - b.ubicacion_stand);
       


        arr?.forEach(el => {
            key = el.ubicacion_stand
            id = el.id_stand


            rows.push(
                <CustomCard arrobject={el} key={"k"+key} id={id} size="sm" number={key} type={1} image="Prev_Estante_3.jpg" handleClick={openEstanteria}>

                </CustomCard>
            )
        })
        
        

        return <Box sx={{ display: "flex", alignItems: "flex-end", padding: "1rem 1rem 0.3rem 1rem" }}>{rows}</Box>;

    }


    return (
        <Box sx={{ width: "97%", height: "100%", display: "flex" }}>
            <Box sx={{ width: '15%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Configuración Stands</Typography>
            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />

            {/* Dividir en componentes */}
            <Box sx={{ width: '85%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>

                <Box sx={{ width: '90%', height: '80%', backgroundColor: '#676767', display: 'flex', overflowX:'auto' }}>

                    {/* Pestañas superiores */}
                    <Box sx={{ width: "auto", height: "1.8rem", position: "absolute", top: "0", display: "flex" }}>
                        <Box id="tab-config" className="o-tab-active" sx={pestanaStyle}><Button sx={{ padding: 0 }} variant="text" id="btn-config">Configuración</Button></Box>
                    </Box>

                    {!infoOpen &&
                        loadCards()
                        //<Box sx={{ display: "flex", alignItems: "flex-end", padding: "1rem" }}>
                            //<CustomCard size="sm" number="1" type={1} image="Prev_Estante_1.jpg" handleClick={openEstanteria}></CustomCard>
                            //<CustomCard size="sm" number="2" type={1} image="Prev_Estante_1.jpg"></CustomCard>
                            //<CustomCard size="sm" number="3" type={1} image="Prev_Estante_3.jpg"></CustomCard>
                            //<CustomCard size="sm" number="4" type={1} image="Prev_Estante_2.jpg"></CustomCard>
                            //<CustomCard size="sm" number="5" type={1} image="Prev_Estante_1.jpg"></CustomCard>
                        //</Box>
                        
                    }



                    {infoOpen &&
                        <MainEstanteria handleClickBack={backEstanteria} arrobject={infoShelf} imgshelf={imgShelf}></MainEstanteria>
                    }


                </Box>
            </Box>
        </Box>
    )
}