const AFRAME = window.AFRAME;
const THREE = window.THREE;

AFRAME.registerComponent('initial-materials', {
  schema: {
    color1: { type: 'string' },
    color2: { type: 'string' }
  },

  init: function () {

    let el = this.el

    // Wait for model to load.
    el.addEventListener('model-loaded', () => {

      //console.log(this.data.color1)
      let c1 = this.data.color1
      let c2 = this.data.color2

      let mesh = el.object3D

      //console.log("COLOR",c1)

      mesh.traverse(function (node) {
        if (node.isMesh) {
          //console.log(node.material.name)
          if (node.material.name === "Mostrador2") {
            //console.log(node)
            node.material.color.set(c1)

          }
          if (node.material.name === "SoporteTV1") {
            //console.log(node)
            node.material.color.set(c2)

          }

        }
      })
      /* // Grab the mesh / scene.
      const obj = el.getObject3D('mesh');
      // Go over the submeshes and modify materials we want.
      obj.traverse(node => {
        if (node.name.indexOf('ship') !== -1) {
          node.material.color.set('red');
        }
      }); */
    });
  }
});


AFRAME.registerComponent('lobby-data', {
  schema: {
    lobbyData: { type: 'string' }
  },
  init: function () {

    let el = this.el
    let mesh = el.object3D
    let data = JSON.parse(this.data.lobbyData)



    let color = data.color_paredes_feria
    let textura = data.textura_suelo_feria


    let sueloTexture = null

    if (textura !== '') {
      sueloTexture = new THREE.TextureLoader().load(
        require('../../../assets/images/Lobby/Texturas/' + textura));
      sueloTexture.wrapS = THREE.RepeatWrapping;;
      sueloTexture.wrapT = THREE.RepeatWrapping;;
      sueloTexture.repeat.set(1, 1)
      sueloTexture.encoding = THREE.sRGBEncoding;
      sueloTexture.name = textura
    }



    let floorMat = new THREE.MeshStandardMaterial({

      //color: '#FFFFFF',
      name: 'Alfombra_Lobby',
      //roughness: 0.8,
      //metalness:0.2,
      map: sueloTexture
    });

    if (mesh) {
      mesh.traverse(function (node) {
        if (node.isMesh) {
          if (node.material.name === "Alfombra_Lobby") {

            node.material = floorMat

          }
          if (node.material.name === "Techo_Pa") {

            node.material.color.set(color)

          }


        }
      })
    }

  }

});
